import React from "react";

import { Button, FormGroup, Tree } from "@blueprintjs/core";
import { useTranslation } from "react-i18next";

import {
  FallbackComponent,
  FormButtonGroup,
  FormContainer,
  Preloader,
} from "../common";

import {
  useAddMissing,
  useRemoveExtraneous,
  useTagProtectionRules,
  useTagsTreeContents,
} from "./hooks";

export const Tags: React.FC = () => {
  const { t } = useTranslation();

  const {
    tagProtectionRules,
    isTagProtectionRulesFetching,
    tagProtectionRulesFetchingError,
  } = useTagProtectionRules();

  const tagsTreeContents = useTagsTreeContents(tagProtectionRules);

  const { addMissing, isAddMissingLoading } = useAddMissing();

  const { removeExtraneous, isRemoveExtraneousLoading } = useRemoveExtraneous();

  const onRemoveExtraneousButtonClick = (): void => {
    if (!confirm(t("tags.confirmRemoveExtraneous"))) {
      return;
    }

    removeExtraneous(tagProtectionRules);
  };

  const onAddMissingButtonClick = (): void => {
    if (!confirm(t("tags.confirmAddMissing"))) {
      return;
    }

    addMissing(tagProtectionRules);
  };

  if (isTagProtectionRulesFetching) {
    return <Preloader />;
  }

  if (tagProtectionRulesFetchingError) {
    return <FallbackComponent error={tagProtectionRulesFetchingError} />;
  }

  return (
    <FormContainer>
      <FormGroup>
        <Tree contents={tagsTreeContents} />
      </FormGroup>
      <FormButtonGroup>
        <Button
          disabled={isRemoveExtraneousLoading}
          onClick={onRemoveExtraneousButtonClick}
        >
          {t("tags.removeExtraneous")}
        </Button>
        <Button
          disabled={isAddMissingLoading}
          onClick={onAddMissingButtonClick}
        >
          {t("tags.addMissing")}
        </Button>
      </FormButtonGroup>
    </FormContainer>
  );
};
